/**
* 
* v-bind="$attrs"inheritAttrs: false styleclass
* 
*/
<template>
    <div>
        <input :value="editor" @input="onContentChange($event.target.value)" v-bind="$attrs"/>
    </div>
</template>

<script>
export default {
    name: "MyInput",
    inheritAttrs: false,
    model: {
        prop: "editor",
        event: "contentChange"
    },
    props: {
        editor: String
    },
    data() {
        return {}
    },
    methods: {
        onContentChange(val) {
            this.$emit('contentChange',val)
        }
    }
};
</script>
